import { useState } from "react";
import './App.css'


const App = () =>{
  const number = useState("");
  const numberTerm = number[0];
  const setNumber= number[1];

  const handleChange = (event) => {
    setNumber(event.target.value);
  };

return(
  <div className="container">
    <div>
    <label htmlFor="phoneNumber">PHONENUMBER:</label>
    <input  type="Number"  id ="screen" name="" onChange={handleChange}/>
    <p>ph number is: <strong>{numberTerm}</strong></p>
    </div>
   <p>HAS DATA PACK :</p>
   <div>
    <input type="radio" id="yes" name="button" value="yes"checked/>
       <label htmlFor="yes">YES</label><br/>
     </div>
     <div>
    <input type="radio" id="yes" name="button" value="NO"checked/>
      <label htmlFor="no">NO</label><br/>
      <button>SUBMIT</button>
      </div>
  </div>
);
};

export default App;




